<template>
  <div id="app" class="container">
    <HelloWorld/>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'

export default {
  name: 'App',
  components: {
    HelloWorld
  }
}
</script>

<style>
  @font-face {
    font-family: Calluna;
    src: url(assets/CallunaSansRegular.otf) format("woff")
  }

  *,
  *:after,
  *:before {
    box-sizing: border-box;
  }

  body,
  html {
    height: 100%;
    -webkit-perspective: 100em;
    perspective: 100em;
    font-family: Calluna, Arial, sans-serif;
    transition: all .3s ease;
    background-color: #f5f5f5;
    position: relative;
    margin: 0
  }

  .container {
    margin: 0 auto;
    max-width: 80em;
    text-align: center;
    height: 100%;
    padding: 0 .5em;
  }
</style>



